@import "../../scss/variable";

.image {
  display: inline-block;
  position: relative;
  border-radius: 10px;
  box-shadow: 0 0 0 10px $colorBorderMyPhoto;
  border-radius: 50%;
  transition: $transition;
  &:hover {
    box-shadow: 0 0 0 10px $colorBorderMyPhotoHover;
  }
  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    overflow: hidden;
    &::after {
      content: "";
      position: absolute;
      background-color: rgba(0, 0, 0, 0.295);
      border-radius: inherit;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
    }
  }
  &__photo {
    display: block;
    background-color: #000;
    position: relative;
  }
}
@media (max-width: 500px) {
  .image {
    box-shadow: 0 0 0 5px $colorBorderMyPhotoHover;
    &:hover {
      box-shadow: 0 0 0 5px $colorBorderMyPhotoHover;
    }
    &__photo {
      max-height: 150px;
      max-width: 150px;
    }
  }
}
