@import "../../../scss/variable";
@import "../../../scss/mixins";

.contact {
  @include forAllPages;
  @include paddingForPages;
  &__wrap {
    @include flexJustifyAlignDirection(center, center, column);
  }
  &__content {
    @include flexJustifyAlignDirection(center, center, column);
  }
  &__mainlinks {
    @include flexJustifyAlignDirection(center, start, column);
    padding: 40px 100px;
    border-radius: 20px;
    background-color: rgba(0, 0, 0, 0.6);
    box-shadow: 4px 13px 16px -5px #000000;
    margin-bottom: 50px;
  }
  &__link {
    padding: 10px 20px;
    margin: 3px 0;
    border-radius: 5px;
    width: 100%;
    display: flex;
    align-items: center;
    text-align: left;
    &-span {
      display: inline-block;
      font-size: 1.3em;
      font-weight: 300;
      color: rgba(255, 250, 250, 0.534);
      max-width: calc(#{$widthSectionMainLinks / 2} - 60px);
      width: 100%;
    }
    &-a {
      @include fontPoppins(300, italic);
      word-wrap: break-word;
      display: block;
      padding: 0 10px;
      color: snow;
      min-width: calc(#{$widthSectionMainLinks / 2} - 60px);
      transition: $transition;
      border-radius: 5px;
      &:hover {
        box-shadow: 0 0 5px 1px $colorBorderMyPhoto;
        background-color: rgba($colorBorderMyPhoto, $alpha: 0.5);
      }
    }
  }
  &__form {
    @include flexJustifyAlignDirection(center, center, column);
    padding: 40px 100px;
    border-radius: 20px;
    background-color: rgba(0, 0, 0, 0.6);
    box-shadow: 4px 13px 16px -5px #000000;
    margin-bottom: 100px;
    max-width: calc(#{$widthSectionMainLinks * 1.7} - 60px);
  }
  &__form-title {
    max-width: $widthSectionMainLinks;
    margin-bottom: 50px;
  }
}
@media (max-width: 670px) {
  .contact {
    &__mainlinks {
      padding: 40px 70px;
    }
    &__form {
      padding: 40px 70px;
    }
  }
}
@media (max-width: 560px) {
  .contact {
    &__mainlinks {
      margin-bottom: 40px;
    }
    &__link {
      padding: 10px 0px;
      &-span {
        font-size: 1.1em;
        max-width: calc(#{$widthSectionMainLinks / 2} - 150px);
      }
    }
    &__form {
      padding: 40px;
      margin-bottom: 50px;
    }
  }
}
@media (max-width: 500px) {
  .contact {
    &__link {
      &-span {
        font-size: 1em;
        max-width: calc(#{$widthSectionMainLinks / 2} - 160px);
      }
      &-a {
        padding: 5px;
      }
    }
  }
}
@media (max-width: 480px) {
  .contact {
    &__mainlinks {
      padding: 40px 10px;
    }
    &__form {
      padding: 40px 10px;
    }
  }
}
@media (max-width: 400px) {
  .contact {
    &__link {
      &-span {
        max-width: calc(#{$widthSectionMainLinks / 2} - 160px);
      }
      &-a {
        font-size: 0.9em;
        padding: 1px;
      }
    }
    &__mainlinks {
      padding: 10px;
    }
    &__form {
      padding: 10px;
    }
  }
}
@media (max-width: 340px) {
  .contact {
    &__link {
      flex-direction: column;
      align-items: start;
      padding-left: 30px;
      &-span {
        margin-bottom: 5px;
      }
      &-a {
        font-size: 0.9em;
        padding: 1px;
        margin: 1px;
        margin-left: 20px;
      }
    }
    &__form-title {
      margin-bottom: 40px;
    }
    &__mainlinks {
      max-width: 300px;
    }
    &__form {
      max-width: 300px;
    }
  }
}
@media (max-width: 300px) {
  .contact {
    &__mainlinks {
      max-width: 280px;
    }
    &__form {
      max-width: 280px;
    }
  }
}